import { useContext } from "react";
import { AppContext } from "./Context/AppContext";
import { Button } from "./Context/Button/Button";
import "./styles.css";

export default function App() {
  const [colourChange, setColourchange] = useContext(AppContext);
  return (
    <div className="App">
      <h1>Context Api</h1>
      <Button> {colourChange} Mode</Button>
      <br />
      <br />
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center"
        }}
      >
        <h3>
          Change Theme
          <label className="switch">
            <input type="checkbox" onClick={() => setColourchange()} />
            <span className="slider round"></span>
          </label>
        </h3>
      </div>
    </div>
  );
}
